.app {
  font-size: 16px;
}

.header {
  padding: 25px 0;
  background-color: #116acc;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.25);
}
.header-container {
  align-items: center;
  justify-content: space-between;
}
.header-buttons-wrapper {
  align-items: center;
  gap: 25px;
}
.header-button {
  font-family: Ubuntu;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25em;
}
.header-button.active {
  background-color: #a0c3ff;
}

.logo {
  color: #fff;
  font-family: Work Sans;
  font-size: 48px;
  font-weight: 300;
}

.auth {
  height: calc(100vh - 105.5px);
}
.auth-container {
  align-items: center;
  justify-content: center;
  height: 100%;
}
.auth-wrapper {
  position: relative;
  min-width: 500px;
  padding: 50px 40px;
}
.auth-title {
  margin: 0 auto;
  margin-bottom: 35px;
  color: #000;
  text-align: center;
}
.auth-form {
  flex-direction: column;
  align-items: flex-start;
  gap: 30px;
