  gap: 12px;
  padding-top: 14px;
  padding-bottom: 14px;
  margin-left: auto;
  line-height: 1.25em;
}
.account-icon-wrapper {
  align-items: center;
  width: 24px;
  height: 24px;
}
.account-card-wrapper {
  gap: 50px 70px;
}
.account-card-wrapper .card {
  grid-column: 4 span;
}

.card {
  flex-direction: column;
  align-items: flex-start;
  gap: 9px;
  padding: 22px;
  background-color: #fff;
  border-radius: 9px;
  box-shadow: 0px 5px 20px 0px rgba(0, 0, 0, 0.25);
}
.card-title {
  margin: 0;
  color: #182233;
  font-family: Roboto;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.2em;
}
.card-balance {
  color: #182233;
  font-size: 14px;
  line-height: 1.4em;
}
.card-inner {
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.card-transaction {
  margin-top: auto;
  font-family: "Work Sans";
  color: #000;
  font-size: 13px;
}
.card-transaction-title {
  margin: 0;
  font-weight: 700;
}
.card-transaction-date {
  font-weight: 400;
}
.card-button {
  padding: 16px 21.5px;
  font-weight: 500;
}

.details {
  padding-top: 44px;
}
.details-container {
  flex-direction: column;
}
.details-inner {
  flex-direction: column;
  row-gap: 24px;
  width: 100%;
  margin-bottom: 50px;
}
.details-title-button-wrapper {
  align-items: center;
  justify-content: space-between;
}
.details-account-info-wrapper {
  align-items: center;
  justify-content: space-between;
}
.details-account-title {
  margin: 0;
  font-family: Work Sans, sans-serif;
  font-weight: 400;
  font-size: 34px;
}
.details-account-balance-wrrapper {
  align-items: center;
  justify-content: space-between;
  min-width: 200px;
  font-family: Work Sans, sans-serif;
}
.details-account-balance-title {
  margin-right: 20px;
}
.details-account-balance-value {
  font-weight: 400;
  font-size: 20px;
}
.details-transfer {
  max-width: 606px;
  padding: 25px 50px;
}

.transfer-form {
  position: relative;
  flex-direction: column;
  gap: 25px;
}
.transfer-form .warning-display {
  top: -10px;
  left: 38%;
