  width: max-content;
  transform: none;
}
.transfer-form-inner {
  flex-direction: column;
  align-items: flex-end;
  gap: 25px;
}
.transfer-input {
  padding: 8px 16px;
}
.transfer-label {
  position: relative;
  align-items: center;
  gap: 18px;
  line-height: 1.25em;
}
.transfer-button-wrapper {
  justify-content: flex-end;
}
.transfer-button {
  align-items: center;
  gap: 10px;
  margin-right: 138px;
  font-weight: 500;
